class TrackElement {
  constructor() {
    this.elements = [];
  }
  isContains(element) {
    return this.elements?.some((node) => node?.id === element);
  }
  add(element) {
    this.elements.unshift(element);
    if (this.elements?.length > 1) this.pause(this.elements?.[1]);
  }
  pause({ self }) {
    self?.el?.setAttribute("aria-hidden", true);
    document.removeEventListener("keydown", self.tabEvent, true);
    document.removeEventListener("focusin", self.focusEvent, true);
  }
  unpause({ self }) {
    self?.el?.setAttribute("aria-hidden", false);
    document.addEventListener("keydown", self.tabEvent, {
      capture: true,
      passive: false
    });
    document.addEventListener("focusin", self.focusEvent, true);
  }
  getLength() {
    return this.elements?.length || 0;
  }

  isTopNode(node) {
    if (!this.elements?.length) return true;
    return this.elements?.[0]?.id === node;
  }
  remove(element) {
    if (this.isContains(element)) {
      this.elements = this.elements.filter((node) => node?.id !== element);
      if (this.elements?.length > 0) this.unpause(this.elements?.[0]);
    }
  }
}

export default TrackElement;
